.event-list {
  flex-flow: column wrap;
  width: 100vw;
  margin: 1em auto;
  display: flex;
  overflow-y: auto;
}

.event-tile {
  background-color: var(--area-color);
  opacity: .9;
  text-wrap: wrap;
  border: 3px solid red;
  border-radius: 12px;
  grid-template-rows: 100%;
  grid-template-columns: 5em auto;
  max-width: 90vw;
  height: auto;
  margin: 10px auto;
  padding: 1em;
  display: grid;
}

.event-details {
  text-wrap: wrap;
  max-width: 82vw;
}

.event-details-icons {
  flex-direction: column;
  justify-content: space-between;
  display: flex;
}

.event-details-title {
  justify-content: space-between;
  align-items: center;
  height: 4em;
  margin-bottom: 1em;
  font-size: 1em;
  font-weight: 500;
  display: flex;
}

.event-details-title-left {
  font-size: 3em;
}

.event-details-footer {
  justify-content: space-between;
  align-items: flex-end;
  height: 3em;
  font-size: 2.5em;
  font-weight: 400;
  display: flex;
}

.event-details-footer-area {
  border: 3px solid #000;
  border-radius: 10px;
  padding: .7em;
}

.event-details-footer-where {
  padding: .3em;
}

.event-details-description {
  font-size: 2.5em;
  font-weight: 200;
}

.event-details-favorites-svg {
  fill: none;
  stroke: currentColor;
  stroke-width: 2px;
  stroke-linecap: round;
  stroke-linejoin: round;
  cursor: pointer;
  width: 1em;
  height: 1em;
  font-size: 2.5rem;
}

.favorited {
  fill: gold;
  color: gold;
  filter: drop-shadow(1px 2px 1px #0006);
}

.menu-accordion-wrapper {
  z-index: 1000;
  position: sticky;
  top: 0;
  box-shadow: 0 -10px 7px 7px #000;
}

.menu-accordion-content {
  cursor: pointer;
  background-color: #d3d3d3;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.menu-accordion-content:hover {
  background-color: #fff;
}

.menu-accordion-content, .menu-accordion-drawer {
  padding: 10px;
}

.menu-accordion-drawer {
  background-color: #d3d3d3;
}

.menu-accordion-heading {
  color: #005b84;
  font-family: Futura Bold, sans-serif;
  font-size: 1.2em;
}

.menu-accordion-svg {
  width: 1em;
  height: 1em;
  font-size: 2.5rem;
  transition: transform .3s;
}

.menu-favorites-svg {
  fill: none;
  stroke: currentColor;
  stroke-width: 2px;
  stroke-linecap: round;
  stroke-linejoin: round;
  cursor: pointer;
  width: 1em;
  height: 1em;
  font-size: 2.5rem;
}

.expanded {
  transform: rotate(180deg);
}

.footer-tab-wrapper {
  background-color: #d3d3d3;
  flex-direction: row;
  justify-content: space-evenly;
  width: 100%;
  padding-top: 15px;
  padding-bottom: 15px;
  display: flex;
  position: fixed;
  bottom: 0;
  box-shadow: 0 10px 7px 7px #000;
}

.footer-tabs {
  font-family: Futura Bold, sans-serif;
  font-size: 1.5em;
  font-variant-caps: small-caps;
}

.footer-tabs > button {
  color: var(--day-color);
  font: inherit;
  cursor: pointer;
  background: none;
  border: none;
  outline: none;
  margin: 0;
  padding: 0;
}

.footer-tab-active {
  text-decoration: underline 5px;
}

@font-face {
  font-family: Futura Bold;
  src: url("Futura Bold.2013bcc4.otf") format("opentype");
}

* {
  font-family: Gill Sans, Gill Sans MT, Calibri, Trebuchet MS, sans-serif;
}

html, body {
  background-color: #262626;
  width: 100vw;
  height: 100vh;
  margin: 0;
  padding: 0;
}
